html {
  --main-blue-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  align-items: center;
  padding-left: 50px;
  display: flex;
}

.nav {
  align-items: center;
  margin-left: auto;
  margin-right: 50px;
  display: flex;
}

ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  margin-right: 20px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--main-blue-color);
  text-decoration: none;
}

.is-active {
  color: var(--main-blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.laptops {
  margin: 0;
}

.gadgets {
  margin-left: 20px;
}

.video {
  margin-right: 0;
